.sunLink1::before {
    content: "";
    position: absolute;
    width: 0px;
    height: 2px;
    bottom: 0px;
    left: 0;
    background-color: #ffffff;
    visibility: hidden;
    transition: all 0.6s cubic-bezier(0.76, 0, 0.24, 1);
  }

  .sunLink1:hover::before {
    visibility: visible;
    width: 100%;
  }

  .sunLink2::before {
    content: "";
    position: absolute;
    width: 0px;
    height: 2px;
    bottom: -2.4px;
    left: 0;
    background-color: #ffffff;
    visibility: hidden;
    transition: all 0.6s cubic-bezier(0.76, 0, 0.24, 1);
  }

