  .wrap {
    border-right: .3vw solid #fff;
    color: #fff;
    animation: caret 1s steps(1) infinite;
    position: relative;
  }
   
  @keyframes caret {
    50% {
      border-color: transparent;
    }
  }

  @keyframes slideIn {
    0% {
      transform: translatey(210%);
    }
    33% {
      transform: translatey(210%);
    }
    100% {
      transform: translatey(0);
    }
    }
    @keyframes mainText {
      0% {
        opacity: 0%;
      }
      50% {
        opacity: 0%;
      }
      100% {
        opacity: 100%;
      }
  }